.accordion {
 
    padding: 0rem  2rem  2rem  2rem;
        border-radius: 3px;
    display: grid;
   /* margin-right: -1rem;  */

 width: auto;
 
    }
    
    
    
    .accordion-visible {
        /* background-color: #ff6060; */
        color: white;
         background-color: #ff6060;
     cursor: pointer;
     display: grid;
   
      align-items: center; 
     height: 50px;
     padding: 7px 15px ;
     border-radius: 5px;
 
     width: max(47.5vw, 200px);

    
    }
    
    
    
    
     .accordion-visible h2 {
  justify-items: center;
    text-align: left;
         font-size: 20px; 
    align-items: center;
    display: flex;
   align-self: center;
      
     } 
    
    .accordion-visible img {
      
    
    margin-top: -4rem;
    
    
    justify-self: end;
    transform: rotate(180deg);
	transition: 0.4s ease-in-out;
    
    }
    .accordion-visible img.active {
   
	transform: rotate(0deg);
	transition: 0.4s 0.4s ease-in-out;
    }
    .accordion-visible span {
       display: grid;
        font-size: 20px;
        padding: 22px 15px;
      pointer-events: none;
    }
   .box-p_text:hover .accordion-visible img:hover{

        cursor: pointer ;
      margin-bottom: 5rem;
    }
    
    .accordion-toggle {
  padding: auto;
        height: 0px;
        font-size: 18px;
        opacity: 0;
        transition:  
         opacity 0.3s ease-in-out, 
         height 0.3s 0.3s ease-in-out; 
       /* / color: #ff6060; */
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        
    }
    
    .animated {
        opacity: 1;
        transition:  
        height 0.3s ease-in-out,
        opacity 0.3s 0.3s ease-in-out;
    }
    
    .accordion-toggle p {
        
      text-size-adjust:content;   
      
        line-height: 1.3;
        /* padding: 22px 15px ; */ 
        
        display: grid;
    }
    .box-p_text{
display:flex;
margin: 0;
padding: 22px 15px;
pointer-events: none;
     
        margin-bottom: 2%;
    
     
}

  
    .box-description {

    grid-area: 1/1;
  
    margin-left: -4rem;   
    display:grid;
       /* width: max(47vw, 200px);    */
   
       /* width:100dvi; */
  


  
   
    }
   
    
    
    
    
    
    
     .box-equipement{ 
    /* margin-bottom: 4rem; */
    display:grid;
    grid-area: 1/2;
       
    
      /* width: max(47vw, 200px);   */
 

 
    }
   
    
    
   

    .wapper_accordion {
    display: grid ;
     margin-right: -8rem;   
    /* margin-left: -3rem; */
    /* height: 52px; */
  
        grid-area: 6/1;  
    grid-template-columns: 2fr 2fr ;
    grid-template-rows: 52px 52px;
  background-color: aqua;
    width:100dvi;
    margin-top: 5rem;
    }
    .accordion .accordion-visible{
    display:grid;
    
    }
   
   

