.herobox {
  background: url('/public/imgs/IMG.png'), #57262655;
  background-blend-mode: darken;
  background-repeat: no-repeat;
  background-size: cover;
  height: 223px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-right: 15px;
}

.legend {
  z-index: 2;
  font-weight: 200;
  font-size: 48px;
  color: #ffffff;
}

section {
  margin-top: 50px;
}

 .box_section_galerie { 
   display: grid; 
   grid-template-columns: repeat(3, 1fr); 
  padding: 3rem; 
   background-color: #f2f2f2f2; 
   border-radius: 30px; 
   margin-top: 2rem; 
   gap: 2rem; 
 } 
 @media (max-width: 1150px) { 
 .box_section_galerie{
 
   grid-template-columns: repeat(2, 1fr); 

 }

}
 @media (max-width: 870px) { 
 .box_section_galerie{
 display: flex;
flex-direction: column;

 }

}
 @media (max-width: 627px) { 
