padding: 0px;

 }

}
 @media (max-width: 627px) { 
 .box_card{

margin-left: -2.5rem;
width: 100%;

 }

}




.box_card {
  border-radius: 15px;
  background-blend-mode: darken;
  height: 340px;
  color: white;
  font-size: 18px;
  padding: 20px;
  display: flex;
  flex-direction: column-reverse;
  background-repeat: no-repeat;
  cursor: pointer;
  transform: scale(1);
  transition: all, 0.3s;
  margin-right:2em;
}
@media screen  and (min-width:200)and(max-width:337px){
  .box_card{
    height: 255px;
  }
}

.box_card:hover {
  transform: scale(1.05);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.box_card_text {
  z-index: 3;
}

.box_card_text p {
  margin: 0;
}
li {
  list-style: none;
}
